.container{
    padding: 20px;
    margin: 10vh 10vw;
    border: 1px solid wheat;
}

p{
    padding: 10px;
    font-family: Monolisa;
    font-size: 30px;
}

@media screen and (max-width: 768px) {

    .container{
        display: 100vw;
        padding: 20px;
        margin: 10vh 10vw;
        border: 1px solid wheat;
    }
    p{
        padding: 10px;
        font-family: Monolisa;
        font-size: 12px;
    }
    

}